import React from "react";

import "./ProvincialTable.css";

export default function ProvincialTable({ covidData }) {
  const data = covidData.map((covid) => {
    const sum = covid.daily_cases.reduce((total, num) => {
      return (total += num.number);
    }, 0);
    const data = {
      province: covid.province,
      positive: sum,
      recoveries: covid.recoveries,
      deaths: covid.deaths,
    };

    return data;
  });

  const totalCovid = (value) =>
    data.reduce((total, cases) => {
      return total + cases[value];
    }, 0);

  return (
    <div className="Provincial-Table">
      <ul>
        <li>Province</li>
        <li>Positive</li>
        <li>Recoveries</li>
        <li>Deaths</li>
        <li>%</li>
      </ul>

      <div>
        {data.map((covid, i) => {
          return (
            <ul key={i}>
              <li>{covid.province}</li>
              <li>{covid.positive}</li>
              <li>{covid.recoveries}</li>
              <li>{covid.deaths}</li>
              <li>
                {((covid.positive / totalCovid("positive")) * 100).toFixed(2)}%
              </li>
            </ul>
          );
        })}
      </div>
      <ul>
        <li>Grand Total</li>
        <li>{totalCovid("positive")}</li>
        <li>{totalCovid("recoveries")}</li>
        <li>{totalCovid("deaths")}</li>
        <li>100%</li>
      </ul>
    </div>
  );
}
