import React, { useState } from "react";
import NationalLineChartGraph from "./NationalLineChartGraph";

import "./NationalLineChart.css";
import DailyLineChartGraph from "./DailyLineChartGraph";

export default function NationalLineChart({ covidData }) {
  const [toggleLineCharts, setToggleLineCharts] = useState(false);

  const onToggleLineCharts = () => {
    setToggleLineCharts(!toggleLineCharts);
  };

  return (
    <>
      <header>
        <h2>Monthly Stats</h2>
        {/* <select
          defaultValue={toggleLineCharts}
          onChange={(event) => setToggleLineCharts(event.target.value)}
        >
          <option value="">National</option>
          <option value="true">Provincial</option>
        </select> */}
      </header>
      <div className="National-LineChart">
        {!toggleLineCharts ? (
          <NationalLineChartGraph
            covidData={covidData}
            onToggleLineCharts={() => setToggleLineCharts(true)}
          />
        ) : (
          <DailyLineChartGraph
            covidData={covidData}
            onToggleLineCharts={onToggleLineCharts}
          />
        )}
      </div>
    </>
  );
}
