import React, { useState } from "react";
import "./SouthAfricanMap.css";
import ProvinceDonutChart from "./ProvinceDonutChart";

import "./SouthAfricanMap.css";

import saMap from "./world-map.svg";
import map from "./southafrica.png";
import downArrow from "./down-arrow.svg";
import upArrow from "./up-arrow.svg";

export default function SouthAfricanMap({ covidData, totalCovid }) {
  const [toggle, setToggle] = useState(false);
  const [toggleCard, setToggleCard] = useState("EC");

  const data = covidData.map((covid) => {
    const sum = covid.daily_cases.reduce((total, num) => {
      return total + num.number;
    }, 0);
    const data = {
      province: covid.province,
      positive: sum,
      recoveries: covid.recoveries,
      deaths: covid.deaths,
    };

    return data;
  });

  let columns = ["EC", "FS", "GP", "KZN", "LP", "MP", "NW", "NC", "WC", "UN"];

  const totalCases = data.reduce((total, num) => {
    return total + num.positive;
  }, 0);

  const onToggle = () => {
    setToggle(!toggle);
  };

  return (
    <>
      {!toggle ? (
        <div className="SA-Map">
          <h4>South African Map</h4>
          <figure>
            <img src={saMap} alt="south african map" />
          </figure>
          <footer>
            <h4>Total Positive Case</h4>
            <h3>{totalCovid("positive")}</h3>
            <button onClick={onToggle}>View map</button>
          </footer>
        </div>
      ) : (
        <div className="statistics-map">
          <button onClick={onToggle}>Return</button>
          <div className="map">
            <figure>
              <img src={map} alt="map" />
            </figure>
          </div>
          <div className="visualization">
            {data.map((column, i) => {
              return (
                <div key={i} className="Card ">
                  <header onClick={() => setToggleCard(columns[i])}>
                    <h3>
                      <span>{columns[i]}</span>
                      {column.province}
                    </h3>
                    {toggleCard !== columns[i] ? (
                      <img src={downArrow} alt="open" />
                    ) : (
                      <img src={upArrow} alt="close" />
                    )}
                  </header>
                  <div
                    style={{
                      display: toggleCard === columns[i] ? "flex" : "none",
                    }}
                    className="stat"
                  >
                    <ul>
                      <li>
                        <span>Positive Cases:</span>
                        <span>{column.positive}</span>
                      </li>
                      <li>
                        <span>Recoveries:</span>
                        <span>{column.recoveries}</span>
                      </li>
                      <li>
                        <span>Deaths:</span>
                        <span>{column.deaths}</span>
                      </li>
                    </ul>
                    <div className="graph">
                      <h4>
                        {((column.positive / totalCases) * 100).toFixed(2)} %
                        <span>Positive %</span>
                      </h4>
                      <ProvinceDonutChart
                        id={"provincechart" + columns[i]}
                        totalCases={totalCases}
                        positiveCase={column.positive}
                      />
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      )}
    </>
  );
}
