import React from "react";
import OverrallDonutChart from "./OverrallDonutChart";

import "./OverralStats.css";

import barIcon from "./graph.svg";
import SouthAfricanMap from "../SouthAfricanMap/SouthAfricanMap";

export default function OverralStats({ covidData, totalCovid }) {
  return (
    <>
      <div className="Overrall-Stats">
        <div className="stats">
          <div className="Card">
            <div>
              <h3>
                <span>
                  {((totalCovid("positive") / 168643) * 100).toFixed(2)}%
                </span>
                <span>Positive</span>
              </h3>
              <OverrallDonutChart totalCovid={totalCovid} tested={207530} />
            </div>

            <footer>
              <img src={barIcon} alt="icon" />
              <h5>Tests Conducted</h5>
              <h4>207530</h4>
            </footer>
          </div>
          <div>
            <div className="Card">
              <img src={barIcon} alt="icon" />
              <h5>Positive</h5>
              <h4>{totalCovid("positive")}</h4>
            </div>
            <div className="Card">
              <img src={barIcon} alt="icon" />
              <h5>Recoveries</h5>
              <h4>{totalCovid("recoveries")}</h4>
            </div>
            <div className="Card">
              <img src={barIcon} alt="icon" />
              <h5>Deaths</h5>
              <h4>{totalCovid("deaths")}</h4>
            </div>
          </div>
        </div>
        <SouthAfricanMap covidData={covidData} totalCovid={totalCovid} />
      </div>
    </>
  );
}
